.body {
    margin-top: 50px;
    margin-bottom: 50px;

}

.navbar-brand {
    margin-right: 100px;
}

#user {
    position: absolute;
    right: 25px;
}

#btnTutor {
    margin-right: 85px;
}

/*....................*/
.header {
    margin-top: 15px;
    justify-content: space-evenly;

}

#images img {
    width: 200px;
    height: 200px;
    margin: 10px 10px 20px 10px;
    /*box-sizing: border-box;*/
}

#title {
    letter-spacing: 2px;
    box-sizing: border-box;
    width: 200px;
    height: 200px;
    margin-left: 3%;
    text-align: center;
    line-height: 2;

}

.academic-course img {
    margin-left: 30%;
    margin-top: 15px;
    margin-bottom: 15px;
}

.payment img {
    width: 450px;
    height: 300px;
}

.payments-online {
    display: flex;
    justify-content: space-evenly;
    margin-top: 15px;
    background-color: floralwhite;
}

.payment-method {
    background-color: floralwhite;
    max-width: 100%;
    display: flex;
    padding-bottom: 15px;
    justify-content: space-around;
}

/*media query*/
@media (max-width: 990px) {
    #viewwork h1 {
        padding-top: 60%;
    }

    #myWork {
        top: 160%;
        grid-template-columns: auto auto;
        justify-content: space-around;
    }

}

@media (max-width: 850px) {
    #viewwork h1 {
        padding-top: 90%;
    }

    #myWork {
        top: 190%;
    }
}

@media (max-width: 768px) {
    #viewwork h1 {
        padding-top: 100%;
    }

    #myWork {
        top: 200%;
        grid-template-columns: auto;
    }
}


